"use strict";
import {RenderConstants,renderNewLayout, initRenderPaging, renderPaging, renderResizeLayout} from '../Views/render';
import doSearch from '../Service/service';
const widthLi = 272;
let wrapperWidthBig = 1360;
const wrapperWidthSmall = 262;
let pageX;
export function search() {
    RenderConstants.submitButton.addEventListener('click', ()=> {
        renderNewLayout();
        doSearch();
        addListeners();
    })
}

function addListeners() {
    window.addEventListener('resize', resizeHandler);
    RenderConstants.divPaging.addEventListener('click', clickNextPage);
    RenderConstants.divWrapper.addEventListener('mousedown', startSwipe);
    RenderConstants.divWrapper.addEventListener('touchstart', startSwipe);
    RenderConstants.divWrapper.addEventListener('mouseup', endSwipe);
    RenderConstants.divWrapper.addEventListener('touchend', endSwipe);
}

function resizeHandler() {
    if (document.body.clientWidth < wrapperWidthBig || document.body.clientWidth > wrapperWidthBig + widthLi) {
        const fiveResultsLi = 1360;
        const fourResultsLi = 1088;
        const threeResultsLi = 816;
        const twoResultsLi = 544;
        let amountLiOnPage;
        let prefAmountLiOnPage = 0;
        let newActivePage;

        if (document.body.clientWidth > wrapperWidthBig) {
            prefAmountLiOnPage--;
        } else {
            prefAmountLiOnPage++;
        }

        const relPrevSizePage = +RenderConstants.divPaging.querySelector("a[class*=active]").getAttribute("rel");
        if (document.body.clientWidth >= fiveResultsLi) {
            wrapperWidthBig = fiveResultsLi;
            amountLiOnPage = 5;
        } else if (document.body.clientWidth < fiveResultsLi) {
            wrapperWidthBig = fourResultsLi;
            amountLiOnPage = 4;

        } else if (document.body.clientWidth < fourResultsLi) {
            wrapperWidthBig = threeResultsLi;
            amountLiOnPage = 3;

        } else if (document.body.clientWidth < threeResultsLi) {
            wrapperWidthBig = twoResultsLi;
            amountLiOnPage = 2;
        }
        prefAmountLiOnPage += amountLiOnPage;
        let rightElemNumber = prefAmountLiOnPage * (relPrevSizePage - 1) + 1;
        newActivePage = rightElemNumber % amountLiOnPage ? Math.floor(rightElemNumber / amountLiOnPage) + 1 : Math.floor(rightElemNumber / amountLiOnPage);
        renderResizeLayout(wrapperWidthBig, newActivePage, amountLiOnPage);
    }
}

function clickNextPage(e) {
    const relPrevActivePage = RenderConstants.divPaging.querySelector("a[class*=active]").getAttribute("rel");
    if (e.target.getAttribute("rel") === "1") {
        goToNextPage(e.target, "2");
        initRenderPaging();
    } else {
        goToNextPage(e.target, relPrevActivePage)
    }
}

function startSwipe(e) {
    RenderConstants.divPaging.querySelector("a[class*=active]").classList.add("fakehover");
    e.preventDefault();
    pageX = e.pageX || e.touches[0].pageX;
}


function endSwipe(e) {
    const pageXNew = e.pageX || e.changedTouches[0].pageX;
    const relPrevActivePage = RenderConstants.divPaging.querySelector("a[class*=active]").getAttribute("rel");
    let page;
    const nextPage = RenderConstants.divPaging.querySelector('a[rel="' + ((+relPrevActivePage) + 1) + '"]');
    const prevPage = RenderConstants.divPaging.querySelector('a[rel="' + ((+relPrevActivePage) - 1) + '"]');
    if (pageX - pageXNew > 20) {
        if (nextPage) {
            page = nextPage;
        } else {
            renderPaging(true);
            page = RenderConstants.divPaging.querySelector('a[rel="' + ((+relPrevActivePage) + 1) + '"]');
        }
    } else {
        if (relPrevActivePage === "1") {
            return;
        } else {
            if (prevPage) {
                page = prevPage;
            } else {
                if ((+relPrevActivePage) - 1 < 4) {
                    initRenderPaging();
                } else {
                    renderPaging(false);
                }
                page = RenderConstants.divPaging.querySelector('a[rel="' + ((+relPrevActivePage) - 1) + '"]');
            }
        }
    }
    goToNextPage(page, relPrevActivePage);

}
function goToNextPage(nextPage, relPrevActivePage) {
    let wrapperWidth;
    let triggerPrev = relPrevActivePage - 1;
    if (!RenderConstants.divPaging.querySelector('a[rel="' + ((+triggerPrev) + 3) + '"]')) {
        doSearch();
    }
    RenderConstants.divPaging.querySelector("a[class*=active]").classList.remove("active");
    let triggerNext = nextPage.getAttribute("rel") - 1;
    nextPage.classList.add('active');
    if (document.body.clientWidth < 544) {
        wrapperWidth = wrapperWidthSmall;
    } else {
        wrapperWidth = wrapperWidthBig;
    }
    RenderConstants.ulResults.animate(
        [{left: (-triggerPrev * wrapperWidth) + 'px'}, {left: (-triggerNext * wrapperWidth) + 'px'}]
        , 500);
    RenderConstants.ulResults.style.left = (-triggerNext * wrapperWidth ) + 'px';
}



