import PlaylistItem from '../Model/model';
import {renderList} from '../Views/render';
const cfgKey = 'AIzaSyD0_KpJho46VKnpCVyEsPVI9KU1uyxPdQI';
let nextPageToken = '';

export default function doSearch() {
    let url = createURL(nextPageToken);
    httpGet(url)
        .then(function (response) {
            nextPageToken = JSON.parse(response).nextPageToken;
            let videos = JSON.parse(response).items;
            let videoIds = '';
            videos.forEach((item) => {
                videoIds += item.id.videoId + ',';
            });
            videoIds.length - 1;
            let statisticURL = 'https://www.googleapis.com/youtube/v3/videos?part=statistics&id=' + videoIds + '&key=' + cfgKey;
            httpGet(statisticURL)
                .then(function (responseStatistic) {
                    var statistic = JSON.parse(responseStatistic).items;
                    videos.forEach((item) => {
                        let statisticItem = _.find(statistic, ['id', item.id.videoId]);
                        let playlistItem = new PlaylistItem(item, statisticItem);
                        renderList(playlistItem);
                    })
                }, function (error) {
                    console.error("Failed!", error);
                })
        }, function (error) {
            console.error("Failed!", error);
        });
}

function createURL(pageToken) {
    const searchValue = document.querySelector('#searchValue').value;
    let url = "https://www.googleapis.com/youtube/v3/search?pageToken=";
    url += pageToken + '&part=snippet&maxResults=15&q=' + searchValue + '&key=' + cfgKey;
    return url;
}

function httpGet(url) {
    return new Promise(function (resolve, reject) {
        var xhr = new XMLHttpRequest();
        xhr.open('GET', url, true);
        xhr.onload = function () {
            if (this.status == 200) {
                resolve(this.response);
            } else {
                var error = new Error(this.statusText);
                error.code = this.status;
                reject(error);
            }
        };

        xhr.onerror = function () {
            reject(new Error("Network Error"));
        };
        xhr.send();
    });
}



